Page({
  data: {
    title: "Hello"
  },
  onLoad() {
    // 页面加载时执行的逻辑，可在此处做一些初始化操作
  },
  goHome() {
    // 跳转首页逻辑，需在 app.json 的 pages 中配置对应页面路径
    wx.navigateTo({
      url: '/pages/home/home' 
    });
  },
  goContact() {
    // 跳转联系我们逻辑，需在 app.json 的 pages 中配置对应页面路径
    wx.navigateTo({
      url: '/pages/contact/contact' 
    });
  },
  goRegister() {
    // 跳转注册页，路径根据实际 app.json 中 pages 配置调整
    wx.navigateTo({
      url: '/pages/register/register' 
    });
  },
  goForgotPassword() {
    // 跳转忘记密码页逻辑，需在 app.json 的 pages 中配置对应页面路径
    wx.navigateTo({
      url: '/pages/forgot-password/forgot-password' 
    });
  },
  handleLogin() {
    // 登录按钮点击逻辑，可在此处获取输入框值、调用登录接口等
    const username = this.getInputValue('#username'); // 假设给输入框加了 id 来获取值，实际可通过双向绑定等方式优化
    const password = this.getInputValue('#password');
    // 示例：调用登录接口
    wx.request({
      url: 'https://your-api-domain.com/login',
      method: 'POST',
      data: {
        username,
        password
      },
      success: (res) => {
        if (res.data.code === 200) {
          wx.showToast({
            title: '登录成功',
            icon: 'success'
          });
          // 登录成功后的逻辑，如跳转首页等
          this.goHome();
        } else {
          wx.showToast({
            title: '登录失败，请检查账号密码',
            icon: 'none'
          });
        }
      },
      fail: (err) => {
        wx.showToast({
          title: '网络异常，请稍后再试',
          icon: 'none'
        });
        console.error('登录请求失败：', err);
      }
    });
  },
  getInputValue(selector) {
    // 简单的获取输入框值的方法，实际可结合小程序的表单组件优化，比如使用 form 标签和 bindsubmit 事件
    const query = wx.createSelectorQuery().in(this);
    return new Promise((resolve) => {
      query.select(selector).fields({ value: true }, (res) => {
        resolve(res.value);
      }).exec();
    });
  }
});